const updateView = function(payload){
    this.component = payload.component
    this.viewData = payload.data
    this.banner = payload.data.banner
}

const setChar = function(char){
    this.component = 'CharacterDetailProfile'
    this.selectedChar = char
    this.viewData = char
    this.banner = char.banner
}

const addToTeam = function(char){
   if(this.currentTeam.length >= 3){
       return
   }
   
   for(let i = 0; i < this.currentTeam.length; i++){
       if(this.currentTeam[i]._id === char._id) return
   }
   
   this.currentTeam.push(char)
}

const startGame = function(){
    this.$socket.client.emit("GAME_START", this.currentTeam);
}

export default {
    updateView,
    addToTeam,
    setChar,
    startGame
}