<template>
  <div>
    <canvas class='selectionScreen__bg' ref='canvas'></canvas>

    <transition name='fade'>
      <main v-if='isloaded' class='selectionScreen'>
        <div class='selectionScreen__section'>
          <Character :id='selectedChar._id' :banner='banner'>
            <!-- EVENTS TRANSMITER: add char to team and change component view -->
            <CharacterDetail
              v-if='selectedChar'
              :character='selectedChar'
              @change-component-view='updateView'
              @add-to-team='addToTeam'
            >
              <component :is='component' :data='viewData'></component>
            </CharacterDetail>
          </Character>

          <Roster @char-selected='setChar'></Roster>
        </div>

        <div class='selectionScreen__panel'>

          <section>
            
          </section>

          <section class='selectionScreen__roster'>
            <div
              class='selectionScreen__roster__picture'
              v-for='(char, key) in currentTeam'
              :key='key'
            >
              <img :src='`/img/game/${char.facepic}.jpg`' />
            </div>
          </section>

          <button @click="startGame" class="selectionScreen__btn">Start Game</button>
          <button class="selectionScreen__btn">Max Raid Battle</button>
          <button class="selectionScreen__btn">Arena Missions</button>
        </div>
      </main>
    </transition>
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";
import {
  BackgroundRenderer,
  loadImage,
  isWebGLSupported,
  TransitionType,
  Easings,
  EffectType
} from "midori-bg";

export default {
  data() {
    return {
      selectedChar: false,
      isloaded: false,
      component: "CharacterDetailProfile",
      viewData: {},
      banner: false,
      currentTeam: []
    };
  },
  components,
  computed,
  methods,
  watchers,
  mounted() {
    if (isWebGLSupported) {
      const renderer = new BackgroundRenderer(this.$refs.canvas);

      loadImage(require("@/assets/img/bg4.jpg"))
        .then(img => {
          renderer.setBackground(img, {
            type: TransitionType.Glitch,
            config: {
              seed: Math.random(),
              duration: 1.5,
              easing: Easings.Cubic.InOut
            }
          });

          const { camera } = renderer.background;

          camera.sway(
            { x: 0.1, y: 0.05, z: 0.02, zr: 1 },
            {
              duration: 2,
              easing: Easings.Quadratic.InOut,
              loop: true
            }
          );

          const { effects } = renderer.background;
          effects.set(EffectType.MotionBlur, { intensity: 1, samples: 32 });
          effects.set(EffectType.RgbShift, { amount: 0.005, angle: 90 });
          effects.set(EffectType.VignetteBlur, {
            size: 3,
            radius: 1.5,
            passes: 10
          });

          const { particles } = renderer.background;

          // generate two named groups of particles in the background.
          particles.generate([
            {
              name: "small",
              amount: 100,
              maxSize: 5,
              maxOpacity: 1,
              minGradient: 0.75,
              maxGradient: 1.0,
              color: 0xffffff
            },
            {
              name: "large",
              amount: 30,
              minSize: 50,
              maxSize: 100,
              maxOpacity: 0.1,
              minGradient: 1.0,
              maxGradient: 1.0,
              color: 0xffffff
            }
          ]);
          particles.sway(
            "large",
            { x: 0.025, y: 0.025 },
            { duration: 1.5, easing: Easings.Sinusoidal.InOut, loop: true }
          );
          particles.sway(
            "small",
            { x: 0.025, y: 0.025 },
            { duration: 1.5, easing: Easings.Sinusoidal.InOut, loop: true }
          );
          const that = this;
          setTimeout(function() {
            that.isloaded = true;
          }, 1250);
        })
        .catch(err => {
          alert(err);
        });
    }
  },
};
</script>

