<template>
  <article class="detail">
    <slot></slot>
    <div class="detail__bottom">
      <img
        class="detail__skill"
        v-for="(skill, key) in character.skills"
        @click="seeSkill(skill)"
        :key="key"
        :src="`/img/game/${skill.skillpic}.jpg`"
      >
      <button class="detail__btn" @click="characterSelect">Add</button>
    </div>
  </article>
</template>

<script>
export default {
  props: ["character"],
  methods: {
    seeSkill(skill) {
      this.$emit("change-component-view", {
        component: "CharacterDetailSkill",
        data: skill
      });
    },
    characterSelect(){
      this.$emit('add-to-team', this.character)
    }
  }
};
</script>

